import Vue from 'vue'
import VueRouter from 'vue-router'
import ChessBoard from '../components/ChessBoard.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ChessBoard',
    component: ChessBoard
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
